@media only screen and (min-width: 600px) {


    body {
        font-size: 150%;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        background: url(./blue-sky-with-shiny-clouds-background.jpg);
        background-size: cover;
        background-position: top;
        background-repeat: no-repeat;
        background-attachment: fixed;
    }

    #head {
        text-align: center;
        color: aliceblue;
        background-color: rgba(240, 248, 255, 0.1);
        font-size: 100px;
        margin: 0px auto;
        margin-top: 20px;
        margin-top: 50px;
        border-top: 2px solid orange;
        border-left: 1px solid orange;
        width: 80%;
        padding: 25px;
        border-radius: 150px 0px 150px 0px;
        text-shadow: 5px 5px 3px orange;
        box-shadow: 5px 5px 5px gray;
    }

    form {
        text-align: center;
        margin-top: 50px;
        margin-bottom: 50px;
    }

    input {
        outline: none;
        border: 0px;
        padding: 20px;
        border-radius: 30%;
        background-color: rgba(275, 275, 275, 0.1);
        box-shadow: 3px 3px 1px gray;
        text-align: center;
    }

    input:focus {
        background-color: rgba(275, 275, 275, 0.1);
    }

    input::placeholder {
        color: orange;
        font-weight: bold;
    }

    #name {
        display: flex;
        align-items: baseline;
        justify-content: center;
        text-shadow: 2px 2px 5px black;
    }

    #countryName {
        margin-left: 10px;
        text-shadow: 2px 2px 5px black;
    }

    /* 
    #details {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        text-align: left;
    } */

    .div {
        /* margin-left: 350px;
        margin-right: 350px;  */
        display: flex;
        justify-content: space-between;
        text-align: left;
    }

    .cHead {
        margin: 10px 20px 0px 100px;
        color: whitesmoke;
        box-shadow: 2px 2px 5px orange;
        border-radius: 15px;
        padding: 15px;
        width: 175px;
        text-align: center;
        text-shadow: 1px 1px 1px gray;
    }

    .content {
        border-radius: 15px;
        box-shadow: 2px 2px 5px whitesmoke;
        padding: 15px;
        width: 175px;
        text-align: center;
        color: orange;
        text-shadow: 1px 1px 1px gray;
        margin: 10px 100px 0px 20px;
    }

    #weatherReport {
        background-color: rgba(100, 100, 100, 0.05);
        padding: 50px;
        /* width: 40% ; */
        border-radius: 120px;
        margin: 0px auto;
        text-align: center;
        /* margin-top: 30px; */
        box-shadow: 5px 5px 10px gray;
    }

    /* .hidden{
        visibility: hidden;
    } */

    #weatherType>h2 {
        margin-bottom: 0px;
        margin-top: 20px;
        text-align: center;
        color: whitesmoke;
        font-size: 50px;
        text-shadow: 2px 2px 5px black;
    }

    /* 
    #temp{
        text-align: end;

    }
     */
    #iconDiv {
        display: flex;
        justify-content: center;
    }

    #wIcon {
        width: 200%;
    }

    #icon {
        margin: 0px 60px 0px 0px;

    }

    #temp>h2 {
        font-size: 80px;
        margin: 50px 0px 0px 0px;
        color: orange;
        text-shadow: 2px 2px 5px black;
    }

    #cityName {
        color: orange;
        text-shadow: 2px 2px 2px grey;
        text-shadow: 2px 2px 5px black;

    }

    #countryName {
        color: whitesmoke;
        text-shadow: 3px 3px 3px grey;
        text-shadow: 2px 2px 5px black;
    }

    .message {
        margin: 20px auto;
        color: red;
        font-weight: bold;
        width: fit-content;
        text-align: center;
        text-shadow: 1px 1px 1px black;
        background-color: rgba(240, 248, 255, 0.1);
        padding: 15px;
        border-radius: 10px;
    }
}