@media only screen and (max-width: 600px) {
  body {
    text-align: left;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    background: url(./blue-sky-with-shiny-clouds-background.jpg);
    background-color: skyblue;
    background-size: cover;
    background-position: top;
    background-repeat: no-repeat;
    background-attachment: fixed;
  }

  #weatherReport {
    margin-top: 30px;
    width: 100%;
  }

  /* 
  #details{
    display: flex;
    flex-wrap: wrap;
    text-align: left;
    justify-content: flex-start;
    margin-left: 5px;
    
  } */

  #div1 {
    text-align: left;
  }

  #temp>h2 {
    font-size: 70px;
    margin: 0px 0px 0px 15px;
    color: white;
    background-color: rgba(100, 100, 100, 0.05);
    text-shadow: 2px 2px 5px black;
  }

  #weatherType>h2 {
    margin: 0px 0px 0px 120px;
    font-size: 40px;
    letter-spacing: 2px;
    color: white;
    text-shadow: 2px 2px 5px black;
  }

  #name {
    display: flex;
    align-items: baseline;
    margin-top: 15px;
    margin-left: 15px;
    color: white;
    background-color: rgba(100, 100, 100, 0.05);
    text-shadow: 2px 2px 5px black;
  }

  #countryName>h3 {
    margin-left: 10px;
    font-size: 25px;
    margin-top: 0px;
    color: white;
    background-color: rgba(100, 100, 100, 0.05);
    text-shadow: 2px 2px 5px black;
  }

  #cityName>h2 {
    font-size: 35px;
    margin-top: 0px;
    color: white;
    text-shadow: 2px 2px 5px black;
  }

  .div {
    display: flex;
    font-size: 30px;
    margin: 15px 0px 0px 0;
  }

  .div>.content {
    margin: 0px 15px 0px 15px;
    color: orange;
    box-shadow: 3px 3px 3px gray;
    padding: 10px;
    background-color: azure;
    border-radius: 20px;
  }

  .div>.cHead {
    border-radius: 20px;
    box-shadow: 3px 3px 3px gray;
    margin: 0px 0px 0px 15px;
    padding: 10px;
    background-color: orange;
    /* rgba(255, 166, 0, 0.6); */
    color: azure;
    font-weight: bold;
    letter-spacing: 2px;
  }

  #head {
    font-size: 60px;
    margin: 0px auto;
    margin-top: 20px;
    font-style: italic;
    text-align: center;
    background-color: rgba(275, 275, 275, 0.2);
    box-shadow: 5px 5px 5px orange;
    border-radius: 70px 0px 70px 0px;
    color: white;
    width: 80%;
    text-shadow: 2px 2px 5px black;

  }

  form {
    text-align: center;
  }

  input {
    outline: none;
    margin-top: 15px;
    border: 0px;
    padding: 15px;
    text-align: center;
    border-radius: 100%;
    background-color: rgba(275, 275, 275, 0.1);
    box-shadow: 3px 3px 5px gray;
  }

  input::placeholder {
    color: orange;
    font-weight: lighter;
  }

  input:focus {
    background-color: rgba(275, 275, 275, 0.1);
  }

  #icon {
    display: flex;
    justify-content: right;
    margin-right: 30px;
  }

  #icon>img {
    width: 150px;
  }

  .div {
    display: flex;
    justify-content: space-between;
  }

  .message {
    margin: 20px auto;
    color: red;
    font-weight: bold;
    width: fit-content;
    text-align: center;
    text-shadow: 1px 1px 1px black;
    background-color: rgba(240, 248, 255, 0.1);
    padding: 15px;
    border-radius: 10px;
  }

}